.vidCont {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.datCont {
    flex-basis: clamp(300px, 45%, 1200px);
    width: 100%;
    color: #fff;
    background-color: #024B6F;
}

.mCont {
    flex-direction: row-reverse;
    text-align: center;
}
.textCont{
    margin-top: 100px;
    padding: 5px 10px;
    background-color: #17171c;
}

.textCont :is(h1, h2, h3) {
    margin: 10px 5px;
    font-family: monospace;
    font-weight: lighter;
}

@media screen and (max-width:1000px) {
    .mCont {
        flex-direction: column;
    }

    .datCont {
        flex-basis: 100%;
        margin: 0;
    }
    .textCont{
        margin: 10px;
    }

}